.TodoItem{
    background: rgb(79,45,223);
    background: radial-gradient(circle, rgba(106, 83, 212, 0.63) 0%, rgba(34, 160, 195, 0.466) 100%);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32,35,41,0.15);
    border-radius: 25px;

}

.TodoItem-p{
    margin: 24px 0 24px 24px;
    width: calc(100%-120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    text-transform: capitalize;
}

.TodoItem-p--complete{
    text-decoration: line-through;

}

.Icon{
    cursor:pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    
}

.Icon-check{
    position: absolute;
    left: 20px;
}

.Icon-check--active{
    color:#4caf50
}

.Icon-delete{
    position:absolute;
    right: 0;
}

.Icon-delete:hover{
    color:rgba(223, 45, 45, 0.596);
}