body {
  margin: 0 auto;
  padding: 0;
  font-family: 'Audiowide', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  color: #d3d3d3;
  background-color:rgb(34,159,195);
  background: linear-gradient(0deg, rgba(34,159,195,1) 0%, rgba(79,45,253,1) 100%);
  max-width: 580px;
  margin: 0 a;

}

#root {
  margin: 0 25px;
  position: relative;
  min-height: 100vh;

}
.TodoCounter{
    font-size: 25px;
    text-transform:capitalize;
    letter-spacing: 2px;
    text-align: center;
    margin: 40px 0 40px;
    padding: 25px;
    border: 2px solid rgb(34,159,195);
    border-radius: 25px;
   

}
.TodoSearch{
    background-color: #f9fbfc;
    border-radius: 2px;
    border: 2px solid rgb(71, 197, 231);
    border-radius: 25px;
    margin: 20px 0;
    height: 75px;
    width: 100%;
    font-size: 24px;
    text-align: center;
    font-family: 'Audiowide', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-weight: 400;
    color: #d3d3d3;
    box-shadow: 0px, 5px,50px, rgba(120, 136, 168, 0.2);
    background-color: rgba(71, 196, 231, 0.329);
    

}

.TodoSearch::-webkit-input-placeholder{
    color: white;
    font-family: 'Audiowide', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-weight: 400;
}

.TodoSearch:-ms-input-placeholder{
    color: white;
    font-family: 'Audiowide', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-weight: 400;
}

.TodoSearch::placeholder{
    color: white;
    font-family: 'Audiowide', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-weight: 400;
}

.TodoSearch :focus{
    outline-color: #61DAFA;
}
ul {
    margin:0;
    padding:0 0 56px 0;
    list-style: none;
}
.CreateTodoButton{
    background-color: #61dafa;
    box-shadow: 0px 5px 25px rgba(97,218, 250,0.5);
    border:none;
    border-radius: 45%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color:#FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width:64px;

    -webkit-transform: rotate(0);

            transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
    
}

.CreateTodoButton:hover{
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
}
.TodoItem{
    background: rgb(79,45,223);
    background: radial-gradient(circle, rgba(106, 83, 212, 0.63) 0%, rgba(34, 160, 195, 0.466) 100%);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32,35,41,0.15);
    border-radius: 25px;

}

.TodoItem-p{
    margin: 24px 0 24px 24px;
    width: calc(100%-120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    text-transform: capitalize;
}

.TodoItem-p--complete{
    text-decoration: line-through;

}

.Icon{
    cursor:pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    
}

.Icon-check{
    position: absolute;
    left: 20px;
}

.Icon-check--active{
    color:#4caf50
}

.Icon-delete{
    position:absolute;
    right: 0;
}

.Icon-delete:hover{
    color:rgba(223, 45, 45, 0.596);
}
.ModalBackground{
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
form{
    width:90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
    border-radius: 25px;
    background: linear-gradient(0deg, rgba(34,159,195,1) 0%, rgba(79,45,253,1) 100%);

}

label{
    text-align: center;
    font-weight: bold;
    font-size: #1e1e1e;
    margin-bottom: 26px;
}

textarea{
    background-color: #F9FBFC;
    border:2px, solid #202329;
    border-radius: 25px;
    box-shadow: 0px 5px 50px rgba(32,35, 42, 0.25);
    color:#1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height:96px;
    width: calc(100%-25px);
    

}

textarea::-webkit-input-placeholder{
    color: #a5a5a5;
    font-family:'Audiowide', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-weight: 400;
}

textarea:-ms-input-placeholder{
    color: #a5a5a5;
    font-family:'Audiowide', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-weight: 400;
}

textarea::placeholder{
    color: #a5a5a5;
    font-family:'Audiowide', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-weight: 400;
}

textarea:focus{
    outline-color: rgb(34,159,195);
}

.buttonContainer{
    margin-top: 14px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

button{
    cursor:pointer;
    display: inline-block;
    font-size: 20px;
    color:#202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 25px;
    border: none;
    font-family: 'Audiowide', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.todoButtonAdd{
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97,218,250,0.5);
}

.todoButtonCancel{
    background-color: rgba(80, 45, 253, 0.356);
    box-shadow: 0px 5px 25px rgba(97,218,250,0.5);
}
.LoadingContainer{
    background: rgb(79,45,223);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32,35,41,0.15);
    border-radius: 25px;

}

.LoadingText{
    margin: 24px 0 24px 24px;
    width: calc(100%-120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    text-transform: capitalize;
}


.LoadingCompleteIcon,
.LoadingDeleteIcon{
    cursor:pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    
}

.LoadingCompleteIcon{
    position: absolute;
    left: 12px;
}


.LoadingDeleteIcon{
    position:absolute;
    top: -24px;
}

.LoadingContainer,
.LoadingCompleteIcon,
.LoadingDeleteIcon{
background: linear-gradient(90deg ,rgba(250,250,250,2),rgb(95, 122, 241));
background-size: 400% 400%;
-webkit-animation: loadingAnimation 3s ease-in-out infinite;
        animation: loadingAnimation 3s ease-in-out infinite;
}


@-webkit-keyframes loadingAnimation {
    0%{
        background-position: 0% 50%;
    }

    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
    
}


@keyframes loadingAnimation {
    0%{
        background-position: 0% 50%;
    }

    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
    
}
